import React, { useState, useEffect } from "react"
import { Section, Heading, ButtonLink } from "components"
import { Row, Col, Form, Skeleton } from "antd"
import Script from "react-load-script"
import styled from "styled-components"

import { baseStyles } from "styles/base"
import { useMutation } from "react-apollo"
import { mutateBasicProfile } from "queries/profile"
import { googleApiKey } from "helpers"
import TextInput from "components/forms/TextInput"
import { educationOptions } from "store/actions/userActions"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { Formik } from "formik"
import { SubmitButton } from "formik-antd"

let autocomplete = null
const MAX_CHAR_LIMIT = 680

function AboutMe({ data = {}, onEditMode, onUserBio, unauthorizedUser, i18n, userData, ...props }) {
	const { fetchUser } = props
	const { location = "", description = "", education = [] } = data
	const { editMode, setEditMode } = onEditMode
	const [_, setAddress] = useState({})
	const [addressValue, setAddressValue] = useState(location)

	const [updateDescription] = useMutation(
		mutateBasicProfile,
		educationOptions(setEditMode, fetchUser, i18n._(t`Your details has been updated`))
	)

	const handleSave = (values, { setSubmitting }) => {
		setSubmitting(false)
		values = { ...values, address: addressValue }
		updateDescription({ variables: { desc: values.description, address: addressValue } })
	}

	const handleCancel = e => {
		e.preventDefault()
		setEditMode("")
	}

	const charAllowed = description => (
		<div>
			<span style={{ color: description && description.length > MAX_CHAR_LIMIT && baseStyles.redColor }}>
				{(description && description.length) || 0}
			</span>{" "}
			<Trans>/ 700 characters allowed</Trans>
		</div>
	)

	const handleLoad = () => {
		const options = {
			types: [],
			componentRestrictions: { country: userData.country }
		}

		/*global google*/
		autocomplete = new google.maps.places.Autocomplete(document.getElementById("location"), options)
		autocomplete.addListener("place_changed", handlePlaceSelect)
	}

	const handlePlaceSelect = () => {
		const addressObject = autocomplete.getPlace()
		const userAddress = addressObject.address_components

		if (userAddress) {
			setAddress({ location: addressObject.formatted_address })
			setAddressValue(addressObject.formatted_address)
		}
	}

	useEffect(() => {
		if (location) setAddressValue(location)
	}, [location])

	// if (Object.keys(userData).length === 0) return <Loading />

	return (
		<Section noPadding marginBottom={0}>
			{editMode === "aboutme" && !unauthorizedUser ? (
				<Formik initialValues={{ address: location, description, education }} onSubmit={handleSave}>
					{({ handleSubmit, values, touched }) => (
						<Form layout="vertical" onSubmit={handleSubmit}>
							<Row gutter={32}>
								<Col lg={24} xs={24}>
									<Script url={googleApiKey} onLoad={handleLoad} />
									<TextInput
										special
										allowClear
										id="location"
										name="address"
										placeholder={i18n._(t`Your location...`)}
										label={<Trans>Your location</Trans>}
										error={touched.address && !addressValue}
										defaultValue={location}
										value={addressValue}
										onChange={e => setAddressValue(e.target.value)}
									/>
								</Col>
								<Col lg={24} xs={24}>
									<TextInput
										textarea
										autoSize={{ minRows: 3 }}
										label={<Trans>Short description</Trans>}
										name="description"
										extra={charAllowed(values.description)}
										maxLength={700}
										placeholder={i18n._(t`Short description about me`)}
									/>
								</Col>
							</Row>
							<Row>
								<Col>
									<SubmitButton type="primary" size="medium">
										<Trans>Save changes</Trans>
									</SubmitButton>{" "}
									&nbsp;{" "}
									<ButtonLink onClick={handleCancel}>
										<Trans>Cancel</Trans>
									</ButtonLink>
								</Col>
							</Row>
						</Form>
					)}
				</Formik>
			) : (
				<Row type="flex" gutter={16}>
					<Col lg={24} xs={24}>
						<Skeleton loading={props.loading} paragraph={{ rows: 2 }} active>
							<BioHeading content={<Trans>Location</Trans>} subheader={addressValue || location || "-"} />
						</Skeleton>
					</Col>
					<Col lg={24} xs={24}>
						<Skeleton loading={props.loading} paragraph={{ rows: 2 }} active>
							<BioHeading
								content={<Trans>Short description</Trans>}
								subheader={
									description ||
									(unauthorizedUser ? (
										<Trans>This tutor didn't set their description yet</Trans>
									) : (
										<Trans>You haven't set your description yet</Trans>
									))
								}
							/>
						</Skeleton>
					</Col>
				</Row>
			)}
		</Section>
	)
}

export default withI18n()(AboutMe)

const BioHeading = styled(Heading).attrs(() => ({
	reverse: true,
	marginBottom: "2em"
}))``
